import { AnyObject } from "@/types";
import { addClass } from "../dom";
import { ComponentPublicInstance } from "vue";

const instances: AnyObject = {}; //  popup vue
const PopupManager: AnyObject = {
  zIndex: 2000,
  modalStack: [],
  nextZIndex() {
    return this.zIndex++;
  },
  getInstance(id: number) {
    return instances[id];
  },
  register(id: number, instance: ComponentPublicInstance) {
    instances[id] = instance;
  },
  deRegister(id: string) {
    instances[id] = null;
    delete instances[id];
  },
  doOnModalClick() {
    //  modal   popup
    //  modal,  idmodal  popup 
    const topItem = this.modalStack[this.modalStack.length - 1];
    const instance = this.getInstance(topItem.id);
    instance.close();
  },
  openModal(id: string, zIndex: number, dom: Element | undefined) {
    const modalDom = this.getModal();
    if (dom) {
      dom.parentNode?.appendChild(modalDom);
    } else {
      document.body.appendChild(modalDom);
    }
    addClass(modalDom, "v-modal");
    modalDom.style.zIndex = zIndex;
    this.modalStack.push({ id: id, zIndex: zIndex});
  },
  closeModal(id: string) {
    const modalDom = this.getModal();
    // modal
    if (this.modalStack.length > 0) {
      const topItem = this.modalStack[this.modalStack.length - 1];
      //   popup
      const { id: popupId } = topItem;
      // popup  modal
      if (id === popupId) {
        this.modalStack.pop(); // pop popupmodal
        if (this.modalStack.length > 0) {
          // pop modal  modalzIndex
          const topItem = this.modalStack[this.modalStack.length - 1];
          const { zIndex } = topItem;
          modalDom.style.zIndex = zIndex;
        } else {
          // mdoal  modal
          if(modalDom.parentNode) modalDom.parentNode.removeChild(modalDom);
        }
      }
    }
  },
  getModal() {
    let modalDom = this.modalDom;
    if (modalDom) {
      return modalDom;
    } else {
      modalDom = document.createElement("div");
      modalDom.addEventListener("click", () => {
        this.doOnModalClick();
      });
      this.modalDom = modalDom;
      return modalDom;
    }
  }
};
export default PopupManager;